import React,{ PropTypes } from 'react';
import s from './Comment.scss';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import DateFormat from '../../util/dateFormat';
import Image from '../Image/Image';
class Comments extends React.Component {

  constructor(props) {
    super(props);
    this.comments = props.comments;
  }
  render() {
    if (!this.comments || this.comments.length === 0) {
      return <noscript />;
    }
    return (
      <div className= { s.comments }>
        <div className= { s.numbers }>
          <span>评论&nbsp;&nbsp;</span>
          <span className={ s.count }>({ this.comments.length })</span>
        </div>
        <ul >
          {
            this.comments.map(item => {
              return <CommentItem key= { item.commentId } data={ item } />;
            })
          }
        </ul>
      </div>
    )
  }
}


function CommentItem({ data }) {
  return (
    <li>
      <div className={s.header}>
        <Image className= { s.avatar } height="48" width="48" src= { data.commentUser.avatar } type="avatar" />
      </div>
      <div className={ s.main }>
        <p className={ s.nickname }>
          {data.commentUser.nickname}
        </p>
        <div className= { s.time }>
          <img src="../../time.png" alt="" />
          <p>
            {
              DateFormat.commentDate(data.createdAt)
            }
          </p>
        </div>
        <p className={s.content}>
          { data.content }
        </p>
        <ul className= { s.images }>
          {
            data.images.map(value => {
              return (
                <li key={ value }>
                  <Image height="102" width="102" src= { value } />
                </li>
              );
            })
          }
        </ul>
      </div>
    </li>
  );
}
export default withStyles(Comments, s);



