import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Strategy.scss';
import Comments from '../../components/Comment/Comments';
import Image from '../../components/Image/Image';
import Audio from '../../components/Audio/Audio';
function renderTools(tools) {
  if (!tools) {
    return null;
  }
  return (
    <div className={ s.tools }>
      <div className= { s.stepHeader }>
        工具
      </div>
      <ul>
        {
          tools.map((value, key) => {
            return (
              <li key={ key }>
                <div>{ value.title }</div>
                <span>{ value.amount }</span>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}
function renderMaterials(materials) {
  if (!materials) {
    return null;
  }
  return (
    <div className={ s.materials }>
      <div className= { s.stepHeader }>
        用料
      </div>
      <ul>
        {
          materials.map((item, key) => {
            return (
              <li key={ key }>
                <div> { item.title } </div>
                <span> { item.amount } </span>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}

function renderSteps(steps) {
  if (!steps) {
    return null;
  }
  return (
    <div className={ s.steps }>
      <div className= { s.stepHeader }>
        步骤
      </div>
      <ul>
        {
          steps.map((item, index) => {
            return (
              <li key={ index }>
                <div className={ s.stepLeft }>
                  <span>
                    { index + 1 }
                  </span>
                </div>
                <div className={ s.stepRight }>
                  <Image src={ item.imgUrl } />
                  <p>{ item.description }</p>
                </div>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}
function renderPhoto(photos) {
  if (!photos.length) {
    return null;
  }
  return (
    <div className= { s.photos }>
      <div className={ s.title }>
        <span>作品&nbsp;&nbsp;</span>
        <span>{ `(${photos.length})` }</span>
      </div>
      <ul>
        {
          photos.map((item, index) => {
            return (
              <li key= { index }>
                <a className= { s.header } href= { `/photos/${item.photoId}/view` } >
                  <Image height="280" width="280" src= { item.content[0].img } />
                </a>
                  <Image className= { s.avatar } height="56" width="56" src= { item.owner.avatar } type="avatar"/>
                <p className= { s.nickname }>{ item.owner.nickname }</p>
                <div className={ s.praiseCount }>
                  <img src="../../good.png" alt="" />
                  <span>{ item.praiseCount }</span>
                </div>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}
function renderTimeCircle(consumingTime){
  if (!consumingTime) {
    return <noscript />;
  }
  let time = '';
  if (consumingTime === 1) {
    time = '< 30 分钟';
  } else if (consumingTime === 2) {
    time = '< 60 分钟';
  } else if (consumingTime === 3) {
    time = '< 90 分钟';
  }
  return (
    <li className="time">
            <span>
              用时
            </span>
      { time }
    </li>
  );
}
function renderScopeCircle(degree) {
  if (!degree || degree === 0) {
    return <noscript />;
  }
  const imgUrl = `../../toolbar_mark${degree}.png`;
  return (
    <li className="level">
            <span>
              难易度
            </span>
      <img src={ imgUrl } alt="" />
    </li>
  );
}
function renderFitCircle(scope) {
  if (!scope) {
    return <noscript />;
  }
  let text = '';
  if (scope === 1) {
    text = '1~2岁';
  } else if (scope === 2) {
    text = '3~4岁';
  } else if (scope === 3) {
    text = '5岁以上';
  }
  return (
    <li className="fit">
            <span>
              适用儿童
            </span>
      { text }
    </li>
  );
}
function renderChildFitWrap(description, consumingTime, degree, scope) {
  if (!description || description.length === 0) {
    return <noscript />;
  }
  return (
    <div className={s.childFitWrap}>
      <ul>
        { renderTimeCircle(consumingTime) }
        { renderScopeCircle(degree) }
        { renderFitCircle(scope) }
      </ul>
    </div>
  );
}
function renderAudio(url) {
  if (!url || url.length <= 0){
    return <noscript />;
  }
  return (
    <div className= { s.player }>
      <Audio src={ url } />
    </div>
  );
}
function Strategy({ strategy }) {
  return (
    <div className={s.container}>
      <div className={ s.cover }>
        <Image height="440" src={ strategy.cover } />
        <div className={s.avatar}>
          <Image height="108" width="108" src={ strategy.owner.avatar } type="avatar" />
          <h3>{ strategy.owner.nickname }</h3>
        </div>
      </div>
      <div className={ s.usedCount }>
        <p>
          {
            `${strategy.tryCount}人参与 综合各评分${strategy.score}分 ${strategy.collectCount}人收藏`
          }
        </p>
      </div>
      { renderChildFitWrap(strategy.description, strategy.consumingTime, strategy.degree, strategy.scope) }
      { renderAudio(strategy.soundStory) }
      <div className= { s.content } dangerouslySetInnerHTML= {{ __html: strategy.description ? strategy.description : '' }} >
      </div>
      <div className= { s.cook }>
        { renderTools(strategy.tools) }
        { renderMaterials(strategy.materials) }
        { renderSteps(strategy.description ? null : strategy.steps) }
      </div>
      { renderPhoto(strategy.photos) }
      <Comments comments={ strategy.comments } />
    </div>
  );
}
Strategy.propTypes = {
  strategy: PropTypes.object.isRequired,
};

export default withStyles(Strategy, s);



